// ===== Helpers =====
const $ = (sel, ctx = document) => ctx.querySelector(sel);
const $$ = (sel, ctx = document) => Array.from(ctx.querySelectorAll(sel));

document.addEventListener('DOMContentLoaded', () => {
  const navbar = $('#navbar');
  const progressBar = $('#progressBar');
  const progressLabel = $('#progressLabel');
  const links = $$('.nav__link');
  const sections = links.map(a => $(a.getAttribute('href')));
  const yearEl = $('#year');

  // Footer year
  if (yearEl) yearEl.textContent = new Date().getFullYear();

  // --- Scroll pipeline (ticked)
  let ticking = false;
  function onScroll() {
    if (!ticking) {
      requestAnimationFrame(() => {
        updateNavbarSize();
        updateProgress();
        updateActiveLink();
        ticking = false;
      });
      ticking = true;
    }
  }

  function updateNavbarSize() {
    const atTop = window.scrollY <= 2;
    navbar.classList.toggle('navbar--large', atTop);
    navbar.classList.toggle('navbar--small', !atTop);
  }

  function updateProgress() {
    const docEl = document.documentElement;
    const scrollTop = docEl.scrollTop || document.body.scrollTop;
    const scrollHeight = docEl.scrollHeight || document.body.scrollHeight;
    const clientHeight = docEl.clientHeight;
    const max = Math.max(scrollHeight - clientHeight, 1);
    const pct = Math.min(Math.max((scrollTop / max) * 100, 0), 100);
    progressBar.style.width = pct + '%';
    progressLabel.textContent = `Reading progress: ${Math.round(pct)}%`;
  }

  function updateActiveLink() {
    const navBottom = navbar.getBoundingClientRect().bottom;

    // At the bottom → force last active
    const nearBottom = Math.abs(window.innerHeight + window.scrollY - document.documentElement.scrollHeight) <= 1;
    let activeIndex = -1;

    if (nearBottom) {
      activeIndex = links.length - 1;
    } else {
      // Section whose top is just below navbar bottom
      let bestDist = Infinity;
      sections.forEach((sec, idx) => {
        if (!sec) return;
        const rect = sec.getBoundingClientRect();
        const dist = rect.top - navBottom;
        if (dist >= -1 && dist < bestDist) {
          bestDist = dist;
          activeIndex = idx;
        }
      });

      // Fallback: if none matched, pick the first section whose bottom is still below nav bottom
      if (activeIndex === -1) {
        sections.forEach((sec, idx) => {
          const rect = sec.getBoundingClientRect();
          if (rect.bottom > navBottom && activeIndex === -1) activeIndex = idx;
        });
      }
    }

    links.forEach((a, i) => a.classList.toggle('is-active', i === activeIndex));
  }

  // Initial paint
  updateNavbarSize();
  updateProgress();
  updateActiveLink();

  // Events
  document.addEventListener('scroll', onScroll, { passive: true });
  window.addEventListener('resize', onScroll);
  window.addEventListener('orientationchange', onScroll);

  // Smooth scroll with offset for sticky navbar
  links.forEach(a => {
    a.addEventListener('click', (e) => {
      const id = a.getAttribute('href');
      if (!id || !id.startsWith('#')) return;
      e.preventDefault();
      const target = document.querySelector(id);
      if (!target) return;

      const navH = navbar.classList.contains('navbar--small')
        ? parseInt(getComputedStyle(navbar).height, 10)
        : parseInt(getComputedStyle(navbar).height, 10);

      const y = window.scrollY + target.getBoundingClientRect().top - (navH + 8);
      window.scrollTo({ top: y, behavior: 'smooth' });
    });
  });

  // ===== Carousel =====
  const carousel = $('[data-carousel]');
  if (carousel) {
    const track = $('[data-carousel-track]', carousel);
    const prevBtn = $('[data-carousel-prev]', carousel);
    const nextBtn = $('[data-carousel-next]', carousel);
    const slides = $$('.carousel__slide', track);
    let index = 0;

    function render() {
      track.style.transform = `translateX(${index * -100}%)`;
    }

    function next() {
      index = (index + 1) % slides.length;
      render();
    }
    function prev() {
      index = (index - 1 + slides.length) % slides.length;
      render();
    }

    nextBtn?.addEventListener('click', next);
    prevBtn?.addEventListener('click', prev);

    // Optional: auto-advance
    let auto = setInterval(next, 5000);
    carousel.addEventListener('mouseenter', () => clearInterval(auto));
    carousel.addEventListener('mouseleave', () => (auto = setInterval(next, 5000)));
  }

  // ===== Modal =====
  const openers = $$('[data-open-modal]');
  const closers = $$('[data-close-modal]');

  openers.forEach(btn => {
    btn.addEventListener('click', () => {
      const sel = btn.getAttribute('data-open-modal');
      const modal = $(sel);
      if (!modal) return;
      modal.classList.add('is-open');
      modal.setAttribute('aria-hidden', 'false');
      const closeBtn = $('.modal__close', modal);
      closeBtn?.focus();
      // ESC key close
      modal.addEventListener('keydown', onEsc);
    });
  });

  closers.forEach(btn => {
    btn.addEventListener('click', closeModalFromChild);
  });

  function onEsc(e) {
    if (e.key === 'Escape') {
      const open = $('.modal.is-open');
      if (open) closeModal(open);
    }
  }

  function closeModalFromChild(e) {
    const modal = e.target.closest('.modal');
    if (modal) closeModal(modal);
  }

  // Backdrop click
  $$('.modal__backdrop').forEach(backdrop => {
    backdrop.addEventListener('click', (e) => {
      const modal = e.target.closest('.modal');
      if (modal) closeModal(modal);
    });
  });

  function closeModal(modal) {
    modal.classList.remove('is-open');
    modal.setAttribute('aria-hidden', 'true');
    modal.removeEventListener('keydown', onEsc);
  }
});